import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FaTrophy, FaVolumeMute } from "react-icons/fa";
import { MdAddShoppingCart } from 'react-icons/md';
import { LuPictureInPicture } from "react-icons/lu";
import { HiOutlineShoppingCart } from "react-icons/hi";
import { IoClose } from "react-icons/io5";
import { FaRegComments } from "react-icons/fa6";
import { MdReviews } from "react-icons/md";
import { GoMute } from "react-icons/go";
import { VscUnmute } from "react-icons/vsc";
import { FaShare } from "react-icons/fa";

const VideoPlayerCard = ({ videoData, settings, setSettings, setButtonValues, buttonValues, colorStates }) => {
  return (
    <div className="container position-relative" style={{ backgroundColor: "#F8F8FF", paddingTop: "15px" }}>
      <div className="row justify-content-center">
        <div className="col-12 col-md-7">
          <div className="card mb-4 " style={{ borderRadius: "20px" }}>
            <video style={{ borderRadius: "20px" }} poster={videoData.cover_image} autoPlay loop muted={settings.isMuteEnabled}>
              <source src={videoData.video_url} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
            <div className='position-absolute end-0 d-flex flex-row align-items-end mt-2'>
              {settings.ispipEnabled &&
                // <div className='position-absolute top-0 end-0 mt-2' 
                // // style={{ marginRight: "20%" }}
                // >
                <LuPictureInPicture color='white' size={22} />
                // </div>
              }
              {settings.isviewcartEnabled && (
                // <div className='position-absolute top-0 end-0 mt-2'
                //  style={{ marginRight: "10%" }}>
                <HiOutlineShoppingCart color='white' size={22} />
                // </div>
              )}
              {/* <div className='position-absolute top-0 end-0 mt-2 me-1'> */}
              <IoClose color='white' size={22} />
              {/* </div> */}
            </div>
            <div className='position-absolute end-0 d-flex flex-column align-items-end' style={{ marginRight: "5%", marginTop: "80%" }}>
              {settings.isMuteEnabled && <GoMute color='white' size={22} className='mb-3' />}
              {!settings.isMuteEnabled && <VscUnmute color='white' size={22} className='mb-3' />}
              {settings.iscommentEnabled && <FaRegComments color='white' size={22} className='mb-3' />}
              {settings.isproductreviewEnabled && <MdReviews color='white' size={22} className='mb-3' />}
              <FaShare color='white' size={22} className='mb-2' />
            </div>

            <div className='d-flex justify-content-center'>
              {settings.isproductTagEnabled && (
                <div className='badge text-dark tag' style={{ position: 'absolute', left: 0, marginLeft: "2.5%", borderRadius: "6px 6px 0 0", zIndex: "888", backgroundColor: "#CD6113" }}>
                  <FaTrophy color='#FFCE31' />&nbsp;&nbsp; Bestseller
                </div>
              )}
              <div className='position-absolute m-2' style={{ borderRadius: "8px", bottom: "0", background: "rgba(255, 255, 255, 0.9)", width: "95%" }}>

                <div className='d-flex flex-wrap'>

                  <div style={{ width: "32%", background: `url(${videoData.image})`, backgroundSize: "cover", backgroundRepeat: "no-repeat", borderRadius: "0px 0px 0px 10px", height: "102px" }}>

                    {/* <img src={videoData.image} style={{ width: "90%", height: "100%", borderRadius: "0 0 0 10px" }} alt="video thumbnail" /> */}

                  </div>
                  <div style={{ width: "68%" }}>


                    <div className='d-flex flex-column ps-2' >
                      <div className='m-0 p-0' style={{ fontSize: "12px", textAlign: "right" }}>
                        <span className='badge text-muted' style={{ backgroundColor: "#FCFCE4" }}> 4.5</span>
                      </div>
                      <span className='product_title_preview' style={{ fontSize: "14px" }}>{videoData.video_title}</span>
                      <span style={{ fontSize: "12px", marginBottom: "3px" }}>${videoData.price}<del className="text-muted">${videoData.discount_price}</del><span className='badge_discount bg-success text-white fw-light rounded ms-1' style={{ padding: "1px" }}><small>30%</small></span></span>

                      {settings.isbuynowEnabled && !settings.isaddtocartEnabled && (<div className='d-flex align-items-center'>
                        <button className='btn btn-sm  mt-2 button-video' style={{ fontSize: "12px", width: "90%", backgroundColor: colorStates.find(initialColors => initialColors.id === 6)?.color, color: colorStates.find(initialColors => initialColors.id === 5)?.color }}>{buttonValues.find(btn => btn.id === 8)?.value}</button>
                      </div>)}
                      {settings.isaddtocartEnabled && !settings.isbuynowEnabled && (<div className='d-flex align-items-center'>
                        <button className='btn btn-sm btn-light  mt-2 button-video' style={{ fontSize: "14px", width: "90%", border: "1px solid black", color: colorStates.find(initialColors => initialColors.id === 7)?.color }}> <MdAddShoppingCart size={23} className='pt-1' style={{ verticalAlign: "bottom", }} />{buttonValues.find(btn => btn.id === 9)?.value}</button>
                      </div>)}
                      {settings.isaddtocartEnabled && settings.isbuynowEnabled && (<div className='d-flex align-items-center gap-2'>
                        <button className='btn btn-sm mt-2 button-video' style={{ fontSize: "12px", width: "75%", backgroundColor: colorStates.find(initialColors => initialColors.id === 6)?.color, color: colorStates.find(initialColors => initialColors.id === 5)?.color }}>{buttonValues.find(btn => btn.id === 8)?.value}</button>
                        <MdAddShoppingCart size={35} className='pe-2 mt-1' />
                      </div>)}
                    </div>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default VideoPlayerCard;
