import React, { useEffect, useState } from 'react';
import { Wizard, useWizard } from 'react-use-wizard';
import 'bootstrap/dist/css/bootstrap.min.css';
import AddVideos from '../components/playlist/AddVideos';
import ManageVideos from '../components/playlist/ManageVideos';
import Createplaylist from '../components/playlist/createPlaylist';
import { FallingLines } from 'react-loader-spinner';
import './playlist.css'
import axios from 'axios';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { useLocation, useNavigate } from 'react-router-dom';
import PlaylistcodeModal from '../components/playlist/PlaylistcodeModal';
const Step1 = ({ title, setTitle, layout, setLayout, orientation, setOrientation, setSelectedVideos }) => {


  const { nextStep } = useWizard();
  return (
    <div>
      <Createplaylist setSelectedVideos={setSelectedVideos} title={title} setTitle={setTitle} layout={layout} setLayout={setLayout} orientation={orientation} setOrientation={setOrientation} />
      <button type='button' className="btn btn-dark mt-5" disabled={!title} onClick={nextStep}>Next</button>
    </div>

  );
};

const Step2 = ({ setSelectedVideos, setLandscapeOffset, setPortraitOffset, fetchData2, setQueryText, selectedVideos, filterProduct, setFilterProduct, queryText, landScapeViewMoreLoading, portraitViewMoreLoading, portraitRowsCount, landscapevideoData, portraitvideoData, fetchData, isPortrait, isLandscape, viewMoreLandscape, viewMorePortrait, landscameRowsCount }) => {

  const { previousStep, nextStep } = useWizard();
  useEffect(() => {

    if (isPortrait) {
      console.log("Orientation is Portrait");

    }
    if (isLandscape) {
      console.log('orientation is landscape')
    }

  }, [isPortrait, isLandscape]);


  return (
    <div>
      <AddVideos setSelectedVideos={setSelectedVideos}
        selectedVideos={selectedVideos}
        portraitvideoData={portraitvideoData}
        landscapevideoData={landscapevideoData}
        videoData={isPortrait ? portraitvideoData : landscapevideoData}
        isPortrait={isPortrait}
        isLandscape={isLandscape}
        viewMoreLandscape={viewMoreLandscape}
        viewMorePortrait={viewMorePortrait}
        fetchData={fetchData}
        landscameRowsCount={landscameRowsCount}
        portraitViewMoreLoading={portraitViewMoreLoading}
        landScapeViewMoreLoading={landScapeViewMoreLoading}
        portraitRowsCount={portraitRowsCount}
        fetchData2={fetchData2}
        filterProduct={filterProduct}
        setFilterProduct={setFilterProduct}
        queryText={queryText}
        setQueryText={setQueryText}
        setPortraitOffset={setPortraitOffset}
        setLandscapeOffset={setLandscapeOffset}
      />
      <button className="btn btn-light mt-2 " style={{ marginRight: '2em' }} onClick={previousStep}>Back</button>
      <button type='button' className="btn btn-dark mt-2 " disabled={!selectedVideos || selectedVideos.length === 0} onClick={nextStep}>Next</button>
    </div>
  );
};

const Step3 = ({ selectedVideos, setSelectedVideos, onPlayListSUbmit, isPortrait, isLandscape, getPlalists }) => {
  const { previousStep } = useWizard();

  // useEffect(() => {

  //   if (isPortrait) {
  //     console.log("Orientation is Portrait");

  //   }
  //   if (isLandscape) {
  //     console.log('orientation is landscape')
  //   }

  // }, [isPortrait, isLandscape]);

  return (
    <div>
      <ManageVideos selectedVideos={selectedVideos} setSelectedVideos={setSelectedVideos} isPortrait={isPortrait} isLandscape={isLandscape} />
      <button className="btn btn-light " style={{ marginRight: '2em', marginTop: "5em" }} onClick={previousStep}>Back</button>
      <button className="btn btn-dark " style={{ marginRight: '2em', marginTop: "5em" }} onClick={onPlayListSUbmit}>Submit</button>
    </div>
  );
};
function extractFieldAndJoin(array, fieldName) {
  // Check if the array is not empty
  if (array.length === 0) {
    return ""; // Return an empty string if the array is empty
  }

  // Extract the specified field from each object
  const values = array.map(obj => obj[fieldName]);

  // Join the values into a comma-separated string
  const result = values.join(", ");

  return result;
}




const Playlist = ({ getPlalists }) => {
  const [filterProduct, setFilterProduct] = useState({});
  const [currentStep, setCurrentStep] = useState(0);
  const [selectedVideos, setSelectedVideos] = useState([]);
  const [title, setTitle] = useState('');
  const [layout, setLayout] = useState('layout1');
  const [orientation, setOrientation] = useState('Portrait')
  const [landscapevideoData, setLandscapevideoData] = useState([])
  const [portraitvideoData, setPortraitvideoData] = useState([])
  const [loading, setLoading] = useState(false)
  const [queryText, setQueryText] = useState("")
  const [landscameRowsCount, setLandscameRowsCount] = useState(0)
  const [portraitRowsCount, setPortraitRowsCount] = useState(0)
  const [isPortrait, setIsPortrait] = useState(false);
  const [isLandscape, setIsLandscape] = useState(false);
  const [portraitOffset, setPortraitOffset] = useState(1)
  const [landscapeOffset, setLandscapeOffset] = useState(1)
  const [landScapeViewMoreLoading, setLandScapeViewMoreLoading] = useState(false)
  const [portraitViewMoreLoading, setPortraitViewMoreLoading] = useState(false)
  const [modal, setModal] = useState(false)
  const user = useSelector(state => state.auth.user)
  const [playListData, setPlalistData] = useState({ shortCode: user.short_code, playlistCode: '' })
  const playlistcodeModalOpen = (row) => {
    setModal(true)
    setPlalistData({ shortCode: user.short_code, playlistCode: row?.playlistCode, onCreatePlaylist: row.onCreatePlaylist })
  }
  const navigate = useNavigate()
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  useEffect(() => {
    if (orientation === 'Portrait') {
      setIsPortrait(true);
      setIsLandscape(false)

    } else {
      setIsPortrait(false);
      setIsLandscape(true);

    }
  }, [orientation]);



  const fetchData = async () => {
    // setLoading(true);
    try {
      await Promise.all([
        axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=1&searchQuery=${queryText}`),
        axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=1&searchQuery=${queryText}`),

        // getProducts()
      ]).then(([landscapeRes, portraitRes]) => {
        setLandscapevideoData(landscapeRes.data.swirls);
        setLandscameRowsCount(landscapeRes.data.rowCount);
        setPortraitvideoData(portraitRes.data.swirls);
        setPortraitRowsCount(portraitRes.data.rowCount);
      });
    } catch (error) {
      console.log(error);
    } finally {
      // setLoading(false);
    }
  }



  const fetchData2 = async () => {
    try {
      const [landscapeRes, portraitRes] = await Promise.all([
        axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=1&searchQuery=${queryText}&productId=${filterProduct.productId}`),
        axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=1&searchQuery=${queryText}&productId=${filterProduct.productId}`)
      ]);
      setLandscapevideoData(landscapeRes.data.swirls);
      setPortraitvideoData(portraitRes.data.swirls);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    fetchData2();
    console.log('FETCH2 IS CALLED')
  }, [filterProduct, queryText])

  useEffect(() => {
    fetchData();
  }, []);
  const onPlayListSUbmit = async () => {
    try {
      await axios.post("https://liveshopping.asia/createPlaylist", {
        payload: {
          videoIds: extractFieldAndJoin(selectedVideos, "video_id"),
          playlistName: title,
          type: layout === "layout1" ? '1' : '3',
          brandId: user.brand_id
        }
      }).then((res) => {
        if (res.data) {
          toast.success("Playlist created")
          getPlalists()
          playlistcodeModalOpen({ shortCode: user.short_code, playlistCode: res.data.playlist.data.playlistCode, onCreatePlaylist: true })
        }
      }).catch((err) => {
        console.log(err);
        toast.error("Something went wrong!")
      })

    } catch (error) {
      console.log(error);
      toast.error("Something went wrong!")
    }
  }

  const viewMorePortrait = async () => {
    setPortraitOffset(portraitOffset + 1)
    setPortraitViewMoreLoading(true)

    await axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=${portraitOffset + 1}&searchQuery=${queryText}`).then((respose) => {
      if (respose.data.swirls) {
        setPortraitvideoData(prevArray => [...prevArray, ...respose.data.swirls]);
        setPortraitViewMoreLoading(false)
      }
    }).catch((ee) => {
      console.log(ee);
      setPortraitViewMoreLoading(false)
    })

    // const y = data
    // setPortraitvideoData(prevArray => [...prevArray, ...y]);
    // setMyArray(y)
  }
  const viewMoreLandscape = async () => {
    setLandscapeOffset(landscapeOffset + 1)
    setLandScapeViewMoreLoading(true)

    await axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=${landscapeOffset + 1}&searchQuery=${queryText}`).then((respose) => {
      if (respose.data.swirls) {
        setLandscapevideoData(prevArray => [...prevArray, ...respose.data.swirls]);
        setLandScapeViewMoreLoading(false)
      }
    }).catch((err) => {
      console.log(err);
      setLandScapeViewMoreLoading(false)
    })
    // const y = data
    // setPortraitvideoData(prevArray => [...prevArray, ...y]);
    // setMyArray(y)
  }
  if (loading) {
    return (
      <div className='w-100 d-grid ' style={{ placeItems: "center", height: "70vh" }}>
        <FallingLines
          color="#EEEE77"
          width="100"
          visible={true}
          ariaLabel="falling-circles-loading"
        />
      </div>
    )
  }

  const setCreateTable = (value) => {
    searchParams.set('createTable', value);
    navigate({ search: searchParams.toString() });
  };

  return (
    <div className="container-fluid mt-4 " >

      <p class="text-secondary ml-2"><span className='pointer_cursor' onClick={() => { setCreateTable(false) }}>Playlist</span><span style={{ marginLeft: '0.7em', marginRight: '0.7em' }}> &gt;</span> <span class="text-primary">CreatePlaylist</span></p>
      <div className='container text-center'>
        <div className="row justify-content-center mb-2">
          <div className="col-3">
            <div className={`step-number ${currentStep >= 0 ? 'bg-eeee77' : ''}`}>1</div>
            <span className={`fw-bold ${currentStep >= 0 ? 'fw-bold-active' : ''}`}>Title and layout</span>
            <span style={{ paddingLeft: '25%' }}> &gt; </span>
          </div>
          <div className="col-3 "  >
            <div className={`step-number ${currentStep >= 1 ? 'bg-eeee77' : ''}`}>2</div>
            <span className={`fw-light ${currentStep >= 1 ? 'fw-bold' : ''}`}>Add Videos </span>
            <span style={{ paddingLeft: '25%' }} > &gt; </span>
          </div>
          <div className="col-3" >
            <div className={`step-number ${currentStep >= 2 ? 'bg-eeee77' : ''} `}>3</div>
            <span className={`fw-light ${currentStep >= 1 ? 'fw-bold' : ''}`}>Manage Videos </span>
          </div>
        </div>
      </div>

      <hr />
      <Wizard onStepChange={(step) => setCurrentStep(step)}>
        <Step1 title={title} setTitle={setTitle} layout={layout} setSelectedVideos={setSelectedVideos} setLayout={setLayout} orientation={orientation} setOrientation={setOrientation} />
        <Step2 setSelectedVideos={setSelectedVideos} setQueryText={setQueryText} setLandscapeOffset={setLandscapeOffset} setPortraitOffset={setPortraitOffset}
          fetchData={fetchData} selectedVideos={selectedVideos} landscapevideoData={landscapevideoData} portraitvideoData={portraitvideoData} isPortrait={isPortrait}
          isLandscape={isLandscape} viewMoreLandscape={viewMoreLandscape} viewMorePortrait={viewMorePortrait} portraitViewMoreLoading={portraitViewMoreLoading} landScapeViewMoreLoading={landScapeViewMoreLoading}
          landscameRowsCount={landscameRowsCount} portraitRowsCount={portraitRowsCount} fetchData2={fetchData2} filterProduct={filterProduct} setFilterProduct={setFilterProduct} queryText={queryText} />
        <Step3 setSelectedVideos={setSelectedVideos} selectedVideos={selectedVideos} onPlayListSUbmit={onPlayListSUbmit} isPortrait={isPortrait} isLandscape={isLandscape} />
      </Wizard>
      <PlaylistcodeModal getPlalists={getPlalists} show={modal} handleClose={() => setModal(false)} playListData={playListData} />
    </div>
  );
};
export default Playlist