import React, { useEffect } from 'react'
import img3 from '../../assets/images/create.png'
import { useLocation, useNavigate } from 'react-router-dom';
const Newplaylist = () => {
  const navigate = useNavigate()
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const setCreateTable = (value) => {
    searchParams.set('createTable', value);
    navigate({ search: searchParams.toString() });
  };


  return (
    <div>
      <div className='text-center p-5' >
        <h5 className='mt-5' >Diving into the world of video playlists</h5>
        <h3 className='fw-bold m-3'>Start your journey with a click, and let the <br />binge-watching adventure begin!</h3>
        <img src={img3} />
        <br />
        <button className='btn btn-dark mt-5' onClick={() => { setCreateTable(true) }}> Create new playlist</button>
      </div>
    </div>
  )
}

export default Newplaylist