import React, { useEffect, useRef, useState } from 'react'
import { Form } from 'react-bootstrap'
import { confirmAlert } from 'react-confirm-alert';
import { FaPlay, FaSpinner } from 'react-icons/fa';
import axios from 'axios';
import { useSelector } from 'react-redux';
import Select from 'react-select';
import { IoIosPhonePortrait } from "react-icons/io";
import { MdOutlineStayCurrentLandscape } from "react-icons/md";
import VideoLandscapeCard from '../components/video-gallery/VideoLandscapeCard';
import VideoPortraitCard from '../components/video-gallery/VideoPortraitCard';
import { FallingLines } from 'react-loader-spinner';
import './videogallery.css'
import { toast } from 'react-toastify';
// isLadscape == 0 means landscape
const VideoGallery = () => {
    const [showCheckboxes, setShowCheckboxes] = useState(false);
    const [selectedCards, setSelectedCards] = useState([]);
    const [products, setProducts] = useState([])
    const [landscapevideoData, setLandscapevideoData] = useState([])
    const [portraitvideoData, setPortraitvideoData] = useState([])
    const [loading, setLoading] = useState(true)
    const [filterOrientation, setFilterOrientation] = useState("0")
    const [portraitOffset, setPortraitOffset] = useState(1)
    const [landscapeOffset, setLandscapeOffset] = useState(1)
    const [landscameRowsCount, setLandscameRowsCount] = useState(0)
    const [portraitRowsCount, setPortraitRowsCount] = useState(0)
    const [FIlterProduct, setFIlterProduct] = useState({ productId: '' })
    const [playinVideoId, setPlayinVideoId] = useState("")
    const [queryText, setQueryText] = useState("")
    const [landScapeViewMoreLoading, setLandScapeViewMoreLoading] = useState(false)
    const [portraitViewMoreLoading, setPortraitViewMoreLoading] = useState(false)
    const toggleCheckboxes = () => {
        setShowCheckboxes(!showCheckboxes);
    };
    console.log(FIlterProduct);
    const user = useSelector(state => state.auth.user)

    const customStyles = {
        control: (provided, state) => ({
            ...provided,
            width: 220, // Adjust the width as needed
        }),
        option: (provided) => ({
            ...provided,
            fontSize: '12px', // Adjust the font size for smaller tags
        }),
    };
    const handleCheckboxChange = (id) => {
        setSelectedCards((prevSelected) => {
            if (prevSelected.includes(id)) {
                return prevSelected.filter((selectedId) => selectedId !== id);
            } else {
                return [...prevSelected, id];
            }
        });
    };


    // const handleDelete = () => {
    //     const newData = landscapevideoData.filter((card) => !selectedCards.includes(card.videoId));
    //     setData(newData);
    //     setSelectedCards([]);
    //     setShowCheckboxes(false)
    // };



    const submit = () => {
        confirmAlert({
            title: "Do you really want to delete the file?",
            // message: "Are you sure to do this.",
            buttons: [
                {
                    label: "Yes, delete",
                    onClick: () => handleDelete()
                },
                {
                    label: "Cancel"
                    // onClick: () => alert("Click No")
                }
            ]
        });
    };
    // const data = 
    const getProducts = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getProductsByBrandId?brandId=${user.brand_id}`).then((res) => {
                if (res.data.products) {
                    setProducts(res.data.products)
                } else {
                    setProducts([])
                }
            })
        } catch (error) {
            console.log(error);
        }
    }

    const handleProductFilter = async (e) => {
        // console.log(e);
        // if (e !== null) {
        //     setFIlterProduct(e)
        // }
        // setTimeout(async () => {
        //     if (e === null) {
        //         await fetchData()
        //     }
        //     await fetchData2()
        // }, 800);

    }



    const handleSearch = async (e) => {
        const query = e.target.value;
        setQueryText(query);
        setPortraitOffset(1);
        setLandscapeOffset(1);

        if (FIlterProduct) {
            // Filter within the selected product
            await handleProductFilter(FIlterProduct, query);
        } else {
            // Perform a general search
            await fetchData(query);
        }
    };

    // const getVideos = async () => {
    //     setLoading(true)
    //     try {
    //         await axios.get(`https://liveshopping.asia/getAllVideosByBrandId?brandId=${user.brand_id}&offsetForPortrait=${1}&offSetForLandscape=${1}&perPageForPortrait=${6}&perPageForLandscape=${4}`).then((res) => {
    //             console.log(res);
    //             if (res.data) {
    //                 setPortraitvideoData(res.data.swirls.isPortrait)
    //                 setLandscapevideoData(res.data.swirls.isLandscape)
    //             }
    //             setLoading(false)
    //         }).catch((err) => {
    //             console.log(err);
    //             setLoading(false)
    //         })
    //     } catch (error) {
    //         (error);
    //         setLoading(false)
    //     }
    // }

    const fetchData = async (duringwhat = "load") => {
        if (duringwhat == "load") {
            setLoading(true);
        }
        // setLoading(true);
        try {
            await Promise.all([
                axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=1&searchQuery=${queryText}`),
                axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=1&searchQuery=${queryText}`),
                getProducts()
            ]).then(([landscapeRes, portraitRes]) => {
                setLandscapevideoData(landscapeRes.data.swirls);
                setLandscameRowsCount(landscapeRes.data.rowCount);
                setPortraitvideoData(portraitRes.data.swirls);
                setPortraitRowsCount(portraitRes.data.rowCount);
            });
            setLoading(false);
        } catch (error) {
            console.log(error);
            setLoading(false);
        } finally {
            setLoading(false);
        }
    }
    const timeoutRef = useRef(null);
    const fetchData2 = async () => {

        try {
            const [landscapeRes, portraitRes] = await Promise.all([
                axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=1&searchQuery=${queryText}&productId=${FIlterProduct.productId}`),
                axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=1&searchQuery=${queryText}&productId=${FIlterProduct.productId}`),
                getProducts()
            ]);

            setLandscapevideoData(landscapeRes.data.swirls);
            setLandscameRowsCount(landscapeRes.data.rowCount);
            setPortraitvideoData(portraitRes.data.swirls);
            setPortraitRowsCount(portraitRes.data.rowCount);
        } catch (error) {
            console.log(error);
        }
    };

    useEffect(() => {
        fetchData();
    }, []);


    useEffect(() => {
        // Clear the previous timeout if it exists
        if (timeoutRef.current) {
            clearTimeout(timeoutRef.current);
        }

        // Set a new timeout
        timeoutRef.current = setTimeout(() => {
            fetchData2();
        }, 400);

        // Cleanup function to clear the timeout when the component unmounts or before the next effect runs
        return () => clearTimeout(timeoutRef.current);
    }, [queryText, FIlterProduct]);
    const viewMorePortrait = async () => {
        setPortraitOffset(portraitOffset + 1)
        setPortraitViewMoreLoading(true)

        await axios.get(`https://liveshopping.asia/getAllPortraitVideoByBrandId?brandId=${user.brand_id}&limit=6&offset=${portraitOffset + 1}&searchQuery=${queryText}`).then((respose) => {
            if (respose.data.swirls) {
                setPortraitvideoData(prevArray => [...prevArray, ...respose.data.swirls]);
                setPortraitViewMoreLoading(false)
            }
        }).catch((ee) => {
            console.log(ee);
            setPortraitViewMoreLoading(false)
        })
        // const y = data
        // setPortraitvideoData(prevArray => [...prevArray, ...y]);
        // setMyArray(y)
    }
    const viewMoreLandscape = async () => {
        setLandscapeOffset(landscapeOffset + 1)
        setLandScapeViewMoreLoading(true)

        await axios.get(`https://liveshopping.asia/getAllLandscapeVideoByBrandId?brandId=${user.brand_id}&limit=8&offset=${landscapeOffset + 1}&searchQuery=${queryText}`).then((respose) => {
            if (respose.data.swirls) {
                setLandscapevideoData(prevArray => [...prevArray, ...respose.data.swirls]);
                setLandScapeViewMoreLoading(false)
            }
        }).catch((err) => {
            console.log(err);
            setLandScapeViewMoreLoading(false)
        })
        // const y = data
        // setPortraitvideoData(prevArray => [...prevArray, ...y]);
        // setMyArray(y)
    }
    if (loading) {
        return (
            <div className='w-100 d-grid ' style={{ placeItems: "center", height: "70vh" }}>
                <FallingLines
                    color="#EEEE77"
                    width="100"
                    visible={true}
                    ariaLabel="falling-circles-loading"
                />
            </div>
        )
    }

    const handleDelete = async () => {

        try {
            await axios.patch("https://liveshopping.asia/updateStatusPerVideoByBrandAndVideoId", {
                videoIds: selectedCards.join(","),
                brandId: user.brand_id
            }).then((res) => {
                toast.success(`${selectedCards.length} videos deleted successfully !`)
                // window.location.reload()
                selectedCards.map((el) => {
                    document.getElementById(`videocard_${el}`).remove()
                })
                setSelectedCards([])
                setShowCheckboxes(false)
            }).catch((error) => {
                console.log(error);
                toast.error("Something went wrong")
            })
        } catch (error) {

        }
    }

    return (
        <div >

            <div className='d-flex w-100 mb-2'>
                <div></div>
                <div className='gap-3 d-flex float-end text-muted float-end ms-auto'>
                    {showCheckboxes && selectedCards.length > 0 && (
                        <button className='btn btn-danger' onClick={submit}>Delete</button>
                    )}
                    {showCheckboxes && (
                        <button className='btn btn-outline-dark' onClick={() => {
                            setSelectedCards([])
                            setShowCheckboxes(false)

                        }}>Cancel</button>
                    )}
                    <Form.Select aria-label="Default select example" onChange={(e) => setFilterOrientation(e.target.value)}>
                        <option value="0">All Orientation</option>
                        <option value="1">Portrait (9:16)</option>
                        <option value="2">Landscape (16:9)</option>
                    </Form.Select>
                    <Select
                        className='w-100'
                        options={products}
                        getOptionLabel={(option) => option.productTitle}
                        getOptionValue={(option) => option.productId}
                        placeholder="Search by product"
                        styles={customStyles}
                        isClearable={true}
                        onChange={async (e) => {
                            setFIlterProduct(e);
                            if (e === null) {
                                await fetchData("clear")
                                setFIlterProduct({ productId: '' })
                            } else {
                                await handleProductFilter();
                            }
                        }}
                    />
                    <Form.Control type='search' placeholder=" Search video..."
                        onChange={(e) => {
                            setPortraitOffset(1);
                            setLandscapeOffset(1);
                            setQueryText(e.target.value);
                            // setFIlterProduct(e);

                        }} />

                </div>
            </div>
            {loading ? "Loading" : <div style={{ height: "calc(100vh - 220px)", overflow: "auto" }}>
                {landscapevideoData.length === 0 && portraitvideoData.length === 0 ? <>
                    <div className="container container_no_videos text-center my-5">
                        <div className="display-6">Nothing found!</div>
                        <div className="lead">Looks like your search went on a video vacation</div>
                        <div className="my-1">

                            <svg width="150" height="226" viewBox="0 0 257 226" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <g clip-path="url(#clip0_2504_16854)">
                                    <path d="M43.2756 85.6198C66.8998 85.6198 86.0511 66.4532 86.0511 42.8099C86.0511 19.1667 66.8998 0 43.2756 0C19.6513 0 0.5 19.1667 0.5 42.8099C0.5 66.4532 19.6513 85.6198 43.2756 85.6198Z" fill="#FF6D40" />
                                    <path d="M108.924 165.435C106.36 165.428 103.84 164.766 101.603 163.51C99.2531 162.227 97.2944 160.331 95.9358 158.023C94.5772 155.715 93.8695 153.081 93.8881 150.402V91.2335C93.8985 88.5541 94.6237 85.9262 95.9885 83.6211C97.3533 81.316 99.3083 79.4174 101.652 78.1214C103.995 76.8255 106.641 76.1792 109.318 76.2493C111.994 76.3194 114.603 77.1034 116.875 78.5203L164.336 108.098C166.498 109.442 168.282 111.317 169.519 113.543C170.756 115.77 171.405 118.275 171.405 120.823C171.405 123.371 170.756 125.876 169.519 128.103C168.282 130.329 166.498 132.203 164.336 133.548L116.875 163.126C114.494 164.625 111.74 165.425 108.927 165.432L108.924 165.435ZM108.924 83.9999C107.685 84.003 106.467 84.3227 105.386 84.9288C104.254 85.5446 103.31 86.4562 102.655 87.5665C102 88.6768 101.658 89.9441 101.665 91.2335V150.402C101.658 151.691 101.999 152.958 102.654 154.068C103.309 155.179 104.252 156.09 105.383 156.707C106.516 157.346 107.8 157.666 109.1 157.634C110.4 157.601 111.667 157.217 112.766 156.523L160.226 126.945C161.267 126.299 162.126 125.399 162.722 124.328C163.317 123.257 163.63 122.052 163.63 120.827C163.63 119.601 163.317 118.396 162.722 117.326C162.126 116.255 161.267 115.354 160.226 114.709L112.766 85.1311C111.616 84.4056 110.284 84.0189 108.924 84.0155V83.9999Z" fill="#454549" />
                                    <path d="M190.95 199.779H64.233C57.6734 199.771 51.3838 197.164 46.7392 192.529C42.0945 187.893 39.4726 181.605 39.4465 175.041V95.9979C39.4465 94.9657 39.8562 93.9758 40.5855 93.2459C41.3148 92.5161 42.3039 92.106 43.3352 92.106C44.3666 92.106 45.3557 92.5161 46.0849 93.2459C46.8142 93.9758 47.2239 94.9657 47.2239 95.9979V175.035C47.2451 179.539 49.0467 183.851 52.2348 187.029C55.4229 190.207 59.7384 191.992 64.2382 191.996H190.945C195.445 191.992 199.76 190.205 202.948 187.026C206.136 183.847 207.936 179.534 207.956 175.03V63.6647C207.935 59.1611 206.134 54.849 202.946 51.6705C199.757 48.4921 195.442 46.7057 190.942 46.7016H96.4806C95.4493 46.7016 94.4602 46.2916 93.7309 45.5617C93.0016 44.8319 92.5919 43.842 92.5919 42.8098C92.5919 41.7776 93.0016 40.7877 93.7309 40.0579C94.4602 39.328 95.4493 38.918 96.4806 38.918H190.945C197.505 38.9268 203.795 41.5342 208.44 46.1703C213.085 50.8064 215.707 57.0942 215.734 63.6595V175.035C215.709 181.601 213.088 187.889 208.444 192.526C203.8 197.163 197.51 199.771 190.95 199.779Z" fill="#454549" />
                                    <path d="M256.228 179.4L240.867 75.8783C239.98 69.8941 236.932 64.4427 232.301 60.5546C227.67 56.6665 221.776 54.6111 215.734 54.7769H214.038C215.149 57.6073 215.724 60.6197 215.734 63.6606V175.036C215.709 181.602 213.088 187.89 208.444 192.527C203.799 197.164 197.51 199.772 190.95 199.781H84.8145V205.032C85.8164 211.493 89.3311 217.295 94.5916 221.172C99.852 225.049 106.431 226.686 112.893 225.726L235.39 207.523C241.88 206.558 247.721 203.053 251.629 197.778C255.537 192.503 257.191 185.891 256.228 179.395V179.4Z" fill="#E8E8E8" />
                                    <g clip-path="url(#clip1_2504_16854)">
                                        <path d="M43 40.0547L53.3125 29.7422L56.2584 32.688L45.9459 43.0005L56.2584 53.313L53.3125 56.2589L43 45.9464L32.6875 56.2589L29.7417 53.313L40.0542 43.0005L29.7417 32.688L32.6875 29.7422L43 40.0547Z" fill="white" />
                                    </g>
                                </g>
                                <defs>
                                    <clipPath id="clip0_2504_16854">
                                        <rect width="256" height="226" fill="white" transform="translate(0.5)" />
                                    </clipPath>
                                    <clipPath id="clip1_2504_16854">
                                        <rect width="50" height="50" fill="white" transform="translate(18 18)" />
                                    </clipPath>
                                </defs>
                            </svg>

                        </div>
                    </div>
                </> : <>
                    {(filterOrientation == 0 || filterOrientation == 1) && <div>

                        {portraitvideoData.length > 0 ? <>
                            <div className='d-block'>
                                <span className='p-3' style={{ borderBottom: "1px solid #17171C" }}>
                                    <IoIosPhonePortrait size={30} style={{ marginLeft: "-10px" }} />  <span className='ms-1'>Portrait</span>
                                </span>
                            </div>
                            <div className='d-flex w-100 flex-wrap mt-3'>
                                {portraitvideoData?.map((el, index) => (
                                    // style={{ width: "236px", height: "400px", minWidth: "236px" }}
                                    <VideoPortraitCard setPlayinVideoId={setPlayinVideoId} playinVideoId={playinVideoId} toggleCheckboxes={toggleCheckboxes} setLoading={setLoading} selectedCards={selectedCards} handleCheckboxChange={handleCheckboxChange} el={el} showCheckboxes={showCheckboxes} index={index} />
                                ))}
                            </div>
                            {portraitvideoData.length >= portraitRowsCount ? "" : <div className='text-center'><button className='btn btn-sm btn-dark bg-dark text-white mt-4' onClick={viewMorePortrait}>View More {portraitViewMoreLoading ? <FaSpinner className="spinning" /> : ""}</button>
                            </div>}</> : ""}


                    </div>}
                    {(filterOrientation == 0 || filterOrientation == 2) && <div className='mt-4'>

                        {landscapevideoData.length > 0 ? <>
                            <div className='d-block'>
                                <span className='p-3' style={{ borderBottom: "1px solid #17171C" }}>
                                    <MdOutlineStayCurrentLandscape size={30} style={{ marginLeft: "-10px" }} />  <span className='ms-1'>Landscape</span>
                                </span>
                            </div>
                            <div className='d-flex w-100 flex-wrap mt-3'>
                                {landscapevideoData?.map((el, index) => (
                                    // style={{ width: "236px", height: "400px", minWidth: "236px" }}
                                    <VideoLandscapeCard setPlayinVideoId={setPlayinVideoId} playinVideoId={playinVideoId} setLoading={setLoading} toggleCheckboxes={toggleCheckboxes} selectedCards={selectedCards} handleCheckboxChange={handleCheckboxChange} el={el} showCheckboxes={showCheckboxes} index={index} />
                                ))}
                            </div>
                            {landscapevideoData.length >= landscameRowsCount ? "" :
                                <div className='text-center'>
                                    <button className='btn btn-sm btn-dark mt-4 bg-dark text-white' onClick={viewMoreLandscape}>View More
                                        {landScapeViewMoreLoading ? <FaSpinner className="spinning" /> : ""}
                                    </button>
                                </div>}</> : ""}

                    </div>}</>}
            </div>}


        </div >

    )
}

export default VideoGallery