import React, { useState, useEffect, useRef } from 'react';
import { IoColorPaletteOutline } from 'react-icons/io5';
import { ChromePicker } from 'react-color';
import { FaEye } from 'react-icons/fa';
import { GoEyeClosed } from "react-icons/go";
import { Button, OverlayTrigger, Popover } from 'react-bootstrap';
const Ecommerce = ({ handleBtnValuesChnage, handleColorChangeInEcommerce, isSeenEnabled, setIsSeenEnabled, handleSwitchChnage, settings, setSettings, initialColors, setColorStates, colorStates, buttonValues, setButtonValues }) => {
    // const [isSeenEnabled, setIsSeenEnabled] = useState(0)
    console.log("seen", isSeenEnabled)
    const handleToggle = (key) => {
        setSettings(prevSettings => {
            const updatedSettings = {
                ...prevSettings,
                [key]: !prevSettings[key],
            };
            ;
            handleSwitchChnage(updatedSettings)
            return updatedSettings;
        });
    };

    const ColorRef1 = useRef()
    const ColorRef2 = useRef()
    const ColorRef3 = useRef()
    useEffect(() => {
        const handleClickOutside = (event) => {

            if (ColorRef1.current && !ColorRef1.current.contains(event.target)) {
                setColorStates(prevColors =>
                    prevColors.map(c => (c.id === 5 ? { ...c, showColorPicker: false } : c))
                );
            }
            if (ColorRef2.current && !ColorRef2.current.contains(event.target)) {
                setColorStates(prevColors =>
                    prevColors.map(c => (c.id === 6 ? { ...c, showColorPicker: false } : c))
                );
            }
            if (ColorRef3.current && !ColorRef3.current.contains(event.target)) {
                setColorStates(prevColors =>
                    prevColors.map(c => (c.id === 7 ? { ...c, showColorPicker: false } : c))
                );
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    // const handleColorChange = (id, color) => {
    //     setColorStates(prevColors => {
    //         const selcolor = prevColors.map(c => (c.id === id ? { ...c, color: color.hex } : c))
    //         handleColorChangeInEcommerce(selcolor)
    //         return color
    //     }
    //     );


    // };

    const handleColorChange = (id, color) => {
        setColorStates((prevColorPickers) => {
            const chnageCOlors = prevColorPickers.map((picker) =>
                picker.id === id ? { ...picker, color: color.hex } : picker
            )
            handleColorChangeInEcommerce(chnageCOlors)
            return chnageCOlors
        }
        );
    };


    const handleClick = (id) => {
        setColorStates(prevColors =>
            prevColors.map(c => (c.id === id ? { ...c, showColorPicker: !c.showColorPicker } : c))
        );
    };
    const getColorState = (id) => colorStates.find(c => c.id === id);


    const handleButtonValueChange = (id, newValue) => {
        setButtonValues(prevValues => {
            const updatedBtnValues = prevValues.map(btn => (btn.id === id ? { ...btn, value: newValue } : btn))
            handleBtnValuesChnage(updatedBtnValues)
            return updatedBtnValues
        }

        );

    };
    const popover = (id) => (
        <Popover style={{ margin: "0", padding: '0' }}>
            <Popover.Body>


                <ChromePicker color={getColorState(id).color} onChange={(color) => handleColorChange(id, color)} />


            </Popover.Body>
        </Popover>
    );

    return (
        <div style={{ height: "72vh", overflowY: 'auto', overflowX: "hidden", padding: "2px" }}>
            <div style={{ backgroundColor: settings.isBuyEnabled ? '#f0f0f0' : '#ffffff', borderRadius: '5px', marginBottom: '6px' }}>
                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="priceSwitch">
                            Buy Now
                            <br />
                            <span className='fs-16'>
                                This option activates the "Buy Now" call to action.
                            </span>
                        </label>
                    </div>
                    <div>
                        <input
                            className="form-check-input switch-lg"
                            type="checkbox"
                            role="switch"
                            id="buySwitch"
                            checked={settings.isBuyEnabled}
                            onChange={() => handleToggle('isBuyEnabled')}
                        />
                    </div>
                </div>

                {settings.isBuyEnabled && (
                    <div className='mt-3 pb-2 ps-3'>
                        <div className='row'>
                            <div className='col-5'>
                                <label className="" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}> Button Text</label>
                                <input
                                    type='text'
                                    className='d-flex form-control'
                                    placeholder='message'
                                    name="productUrl"
                                    value={buttonValues.find(btn => btn.id === 8)?.value || ''}
                                    onChange={(e) => handleButtonValueChange(8, e.target.value)}
                                />
                            </div>
                            <div className='col-5'>
                                <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}> Text color</label>
                                <OverlayTrigger trigger="click" placement="bottom" overlay={popover(5)} rootClose>
                                    <div className="input-group mb-3">
                                        <span className="input-group-text">
                                            <span className="color_badge" style={{ backgroundColor: getColorState(5).color }}></span>
                                        </span>
                                        <input
                                            type="text"
                                            value={getColorState(5).color}
                                            onClick={() => handleClick(5)}
                                            className="form-control pointer_cursor"
                                        />
                                        <span className="input-group-text pointer_cursor" onClick={() => handleClick(5)}>
                                            <IoColorPaletteOutline />
                                        </span>
                                    </div>

                                </OverlayTrigger>


                            </div>
                        </div>
                        <div className='row'>
                            <div className='col-10'>
                                <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}>Background color</label>
                                <OverlayTrigger trigger="click" placement="bottom" overlay={popover(6)} rootClose>
                                    <div className="input-group mb-3">
                                        <span className="input-group-text">
                                            <span className="color_badge" style={{ backgroundColor: getColorState(6).color }}></span>
                                        </span>
                                        <input
                                            type="text"
                                            value={getColorState(6).color}
                                            onClick={() => handleClick(6)}
                                            className="form-control pointer_cursor"
                                        />
                                        <span className="input-group-text pointer_cursor" onClick={() => handleClick(6)}>
                                            <IoColorPaletteOutline />
                                        </span>
                                    </div>
                                </OverlayTrigger>
                                {/* {getColorState(6).showColorPicker && (
                                    <div ref={ColorRef2} style={{ position: 'absolute', zIndex: '2' }}>
                                        <ChromePicker color={getColorState(6).color} onChange={(color) => handleColorChange(6, color)} />
                                    </div>
                                )} */}
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <div style={{ backgroundColor: settings.isAddtoCartEnabled ? '#f0f0f0' : '#ffffff', borderRadius: '5px' }}>
                <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
                    <div>
                        <label className="fs-18 form-check-label ps-3" htmlFor="priceSwitch">
                            Add to Cart
                            <br />
                            <span className='fs-16'>
                                This option activates the "Add to cart" call to action.
                            </span>
                        </label>
                    </div>
                    <div>
                        <input
                            className="form-check-input switch-lg"
                            type="checkbox"
                            role="switch"
                            id="addtocartSwitch"
                            checked={settings.isAddtoCartEnabled}
                            onChange={() => handleToggle('isAddtoCartEnabled')}
                        />
                    </div>
                </div>

                {settings.isAddtoCartEnabled && (
                    <div className='mt-2 pb-2 ps-3'>
                        <div className='row'>
                            <div className='col-5'>
                                <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}>Button text</label>
                                <input
                                    type='text'
                                    className='d-flex form-control'
                                    placeholder='Add to Cart'
                                    name="productUrl"
                                    value={buttonValues.find(btn => btn.id === 9)?.value || ''}
                                    onChange={(e) => handleButtonValueChange(9, e.target.value)}
                                />
                            </div>
                            <div className='col-5'>
                                <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}> Text color</label>
                                <OverlayTrigger trigger="click" placement="bottom" overlay={popover(7)} rootClose>
                                    <div className="input-group mb-3">

                                        <span className="input-group-text">
                                            <span className="color_badge" style={{ backgroundColor: getColorState(7).color }}></span>
                                        </span>
                                        <input
                                            type="text"
                                            value={getColorState(7).color}
                                            onClick={() => handleClick(7)}
                                            className="form-control pointer_cursor"
                                        />
                                        <span className="input-group-text pointer_cursor" onClick={() => handleClick(7)}>
                                            <IoColorPaletteOutline />
                                        </span>
                                    </div>
                                </OverlayTrigger>
                                {/* {getColorState(7).showColorPicker && (
                                    <div ref={ColorRef3} style={{ position: 'absolute', zIndex: '2' }}>
                                        <ChromePicker color={getColorState(7).color} onChange={(color) => handleColorChange(7, color)} />
                                    </div>
                                )} */}
                            </div>
                        </div>
                        <div className='row'>
                            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-1">
                                <div>
                                    <label className=" fs-18 form-check-label ps-2 " htmlFor="autoplaySwitch">
                                        Product quantity
                                        <br />
                                        <span className='fs-16'>
                                            Enables selection of product quantity.
                                        </span>
                                    </label>
                                </div>
                                <div>
                                    <input
                                        className="form-check-input switch-lg"
                                        type="checkbox"
                                        role="switch"
                                        id="ProductQuantitySwitch"
                                        checked={settings.isProductquantityEnabled}
                                        onChange={() => handleToggle('isProductquantityEnabled')}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className='row'>
                            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 " style={{ paddingLeft: "6px" }}>
                                <div>
                                    <label className=" fs-18 form-check-label ps-2 " htmlFor="autoplaySwitch">
                                        Add to cart popup
                                        <br />
                                        <span className='fs-16'>
                                            Enable the dialog box for add-to-cart confirmation.
                                        </span>
                                    </label>

                                </div>

                                {settings.isCardpopup ? (
                                    isSeenEnabled ? <GoEyeClosed
                                        size={30}
                                        style={{ marginTop: "1%" }}
                                        onClick={() => { setIsSeenEnabled(0) }}
                                    /> : <FaEye
                                        size={30}
                                        style={{ marginTop: "1%" }}
                                        onClick={() => { setIsSeenEnabled(1) }}
                                    />) : (
                                    <>
                                    </>)}




                                <div>

                                    <input
                                        className="form-check-input switch-lg"
                                        type="checkbox"
                                        role="switch"
                                        id="cartpopupSwitch"
                                        checked={settings.isCardpopup}
                                        onChange={(e) => {
                                            handleToggle('isCardpopup')
                                            if (settings.isCardpopup) {
                                                // setSettings(prevSettings => {
                                                //     const updatedSettings = {
                                                //         ...prevSettings,
                                                //         ["isSeenEnabled"]: 0,
                                                //     };

                                                //     return updatedSettings;
                                                // });
                                                setIsSeenEnabled(0)

                                            }

                                        }}
                                    />

                                </div>
                            </div>
                            {settings.isCardpopup && (
                                <div>
                                    <div className='col-10'>
                                        <label className="mt-2 mb-1 " style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}> Success Message</label>
                                        <input
                                            type='text'
                                            className='m-2 ms-0 d-flex form-control'
                                            placeholder='Added to Cart'
                                            name="productUrl"
                                            value={buttonValues.find(btn => btn.id === 10)?.value || ''}
                                            onChange={(e) => handleButtonValueChange(10, e.target.value)}
                                        />
                                    </div>
                                    <div className='row'>
                                        <div className='col-5'>
                                            <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}> View cart button text</label>
                                            <input
                                                type='text'
                                                className='m-2 ms-0 d-flex form-control'
                                                placeholder='View cart'
                                                name="productUrl"
                                                value={buttonValues.find(btn => btn.id === 11)?.value || ''}
                                                onChange={(e) => handleButtonValueChange(11, e.target.value)}
                                            />
                                        </div>
                                        <div className='col-5'>
                                            <label className="mt-1 mb-1" style={{ fontWeight: "lighter", fontSize: "13px", color: '#454549' }}>Shopping text button</label>
                                            <input
                                                type='text'
                                                className='m-2 d-flex form-control'
                                                placeholder='Continue shopping'
                                                name="productUrl"
                                                value={buttonValues.find(btn => btn.id === 12)?.value || ''}
                                                onChange={(e) => handleButtonValueChange(12, e.target.value)}
                                            />
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default Ecommerce;
