import React, { useState, useEffect } from 'react'

const VideoplayerSetting = ({ handleSwitchChnage, settings, setSettings }) => {

  const handleToggle = (key) => {
    setSettings(prevSettings => {
      const updatedSettings = {
        ...prevSettings,
        [key]: !prevSettings[key],
      };
      ;
      handleSwitchChnage(updatedSettings)
      return updatedSettings;
    });
  };


  return (
    <div style={{ maxHeight: '70vh', overflowY: 'auto' }}>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="muteSwitch">
            Mute
            <br />
            <div className='fs-16'>
              For first-time viewers, the video is muted.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="muteSwitch"
            checked={settings.isMuteEnabled}
            onChange={() => handleToggle('isMuteEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="pipSwitch">
            Picture-in-Picture
            <br />
            <div className='fs-16'>
              Watch videos in a small, floating window with Picture-in-Picture mode.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="pipSwitch"
            checked={settings.ispipEnabled}
            onChange={() => handleToggle('ispipEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="previewSwitch">
            Video preview thumbnail (Desktop only)
            <br />
            <div className='fs-16'>
              Preview the next and previous videos.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="previewSwitch"
            checked={settings.ispreviewEnabled}
            onChange={() => handleToggle('ispreviewEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="previewSwitch">
            Video download
            <br />
            <div className='fs-16'>
              User can download video with permission.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="videodownloadSwitch"
            checked={settings.isvideodownloadEnabled}
            onChange={() => handleToggle('isvideodownloadEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="previewSwitch">
            Product Tag
            <br />
            <div className='fs-16'>
              This option enables product tagging for attached items.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="productTagSwitch"
            checked={settings.isproductTagEnabled}
            onChange={() => handleToggle('isproductTagEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="commentSwitch">
            Comment on Video
            <br />
            <div className='fs-16'>
              Allows users to comment on the video.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="commentSwitch"
            checked={settings.iscommentEnabled}
            onChange={() => handleToggle('iscommentEnabled')}
          />
        </div>
      </div>
      <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
        <div>
          <label className=" fs-18 form-check-label ps-2 " htmlFor="previewSwitch">
            Product Review
            <br />
            <div className='fs-16'>
              To display a product review for each product associated with the video.
            </div>
          </label>
        </div>
        <div>
          <input
            className="form-check-input switch-lg"
            type="checkbox"
            role="switch"
            id="productreviewSwitch"
            checked={settings.isproductreviewEnabled}
            onChange={() => handleToggle('isproductreviewEnabled')}
          />
        </div>
      </div>
      <div style={{ backgroundColor: settings.isctaEnabled ? '#F5F5F5' : 'white' }}>
        <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
          <div>
            <label className=" fs-18 form-check-label ps-2 " htmlFor="ctaSwitch">
              Call to action
              <br />
              <div className='fs-16'>
                Encourage users to take action with a compelling call to action.
              </div>
            </label>
          </div>
          <div>
            <input
              className="form-check-input switch-lg"
              type="checkbox"
              role="switch"
              id="ctaSwitch"
              checked={settings.isctaEnabled}
              onChange={() => {
                handleToggle('isctaEnabled')

                if (settings.isctaEnabled) {
                  setSettings(prevSettings => {
                    const updatedSettings = {
                      ...prevSettings,
                      ["isbuynowEnabled"]: false,
                      ["isaddtocartEnabled"]: false,
                      ["isviewcartEnabled"]: false,

                    };
                    ;
                    return updatedSettings;
                  });
                }

              }}
            />
          </div>
        </div>
        {settings.isctaEnabled && (
          <div >
            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
              <div>
                <label className="fs-18 form-check-label ps-2" htmlFor="priceSwitch">
                  Buy now
                  <br />
                  <div className='fs-16'>
                    This option activates the "Buy Now" call to action.
                  </div>
                </label>
              </div>
              <div>
                <input
                  className="form-check-input switch-lg"
                  type="checkbox"
                  role="switch"
                  id="buynowSwitch"
                  checked={settings.isbuynowEnabled}
                  onChange={() => handleToggle('isbuynowEnabled')}
                />
              </div>
            </div>
            <div >
              <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
                <div>
                  <label className="fs-18 form-check-label ps-2" htmlFor="priceSwitch">
                    Add to Cart
                    <br />
                    <div className='fs-16'>
                      This option activates the "Add to cart" call to action.
                    </div>
                  </label>
                </div>
                <div>
                  <input
                    className="form-check-input switch-lg"
                    type="checkbox"
                    role="switch"
                    id="addtocartSwitch"
                    checked={settings.isaddtocartEnabled}
                    onChange={() => handleToggle('isaddtocartEnabled')
                    }
                  />
                </div>
              </div>
            </div>
          </div>

        )}
        {settings.isaddtocartEnabled && settings.isctaEnabled && (

          <div>
            <div className="form-check form-switch d-flex justify-content-between align-items-center pt-2 ps-0">
              <div>
                <label className=" fs-18 form-check-label ps-2 " htmlFor="muteSwitch">
                  View Cart
                  <br />
                  <div className='fs-16'>
                    To enable the shopping cart icon for product added to cart.
                  </div>
                </label>
              </div>
              <div>
                <input
                  className="form-check-input switch-lg"
                  type="checkbox"
                  role="switch"
                  id="cartSwitch"
                  checked={settings.isviewcartEnabled}
                  onChange={() => handleToggle('isviewcartEnabled')}
                />
              </div>
            </div>
          </div>

        )}
      </div>
    </div>
  )
}

export default VideoplayerSetting;