// export default PlaylistCard;
import React from 'react'
import { FaTrophy, FaEye } from 'react-icons/fa';

const PlaylistCard = ({ videoData, settings, colorPickers }) => {
  return (
    <div className="container-fluid d-flex justify-content-center align-items-center p-2 pt-5 pb-5" style={{ backgroundColor: "#F8F8FF", marginTop: "10vh", borderRadius: "5px" }}>
      <div className='position-relative col-12 '>
        {settings.isAutoplayEnabled ? (
          <video className="card-img-top" style={{ borderRadius: "20px" }} poster={videoData.cover_image} autoPlay loop muted>
            <source src={videoData.video_url} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        ) : (
          <img className="card-img-top" src={videoData.cover_image} style={{ borderRadius: "20px" }} alt={videoData.video_title} />
        )}
        <div className='d-flex flex-column position-absolute top-0 w-100 h-100'>
          {settings.isTagEnabled && (
            <span className="badge position-absolute top-0 start-0" style={{ borderRadius: "12px 2px", backgroundColor: "#CD6113", fontWeight: "lighter" }}>
              <FaTrophy className='pe-1' color="#F3CE58" size={20} />
              <span className='d-none d-sm-inline'>Best Seller</span>
            </span>
          )}
          {settings.isViewsEnabled && (
            <span className="badge position-absolute top-0 end-0" style={{ fontSize: '1rem', color: "#000" }}>
              <FaEye size={20} style={{ marginRight: "5px" }} />
              <span className='ms-1'>30</span>
            </span>
          )}
        </div>
        {settings.isVideoTitleEnabled && !settings.isProductEnabled && (
          <div style={{ position: "absolute", bottom: "0", width: "100%", background: "linear-gradient(180deg, rgba(42, 41, 41, 0) -8.49%, rgba(42, 41, 41, 0.22) 16.38%, #000000 84.5%)", zIndex: "1" }}>
            <p className="card-title text-white m-0 p-2" style={{
              fontSize: "1rem", overflow: "hidden",
              whiteSpace: "nowrap",
              textOverflow: "ellipsis"
            }}>{videoData.video_title}</p>
          </div>
        )}
        {settings.isProductEnabled && !settings.isPriceEnabled && (
          <div style={{ position: "absolute", bottom: "0", width: "100%", background: "linear-gradient(180deg, rgba(42, 41, 41, 0) -8.49%, rgba(42, 41, 41, 0.22) 16.38%, #000000 84.5%)", zIndex: "1" }}>
            <div className="d-flex align-items-center p-2">
              <img className='img-fluid' src={videoData.image} alt="Thumbnail" style={{ maxWidth: "45px", maxHeight: "45px", borderRadius: "2px", border: "2px solid white" }} />
              <p className="card-title text-white m-0 ms-2" style={{
                fontSize: "1rem", flex: "1", overflow: "hidden",
                whiteSpace: "nowrap",
                textOverflow: "ellipsis"
              }}>{videoData.video_title}</p>
            </div>
          </div>
        )}
        {settings.isPriceEnabled && settings.isProductEnabled && (
          <div style={{ position: "absolute", bottom: "0", width: "100%", background: "linear-gradient(180deg, rgba(42, 41, 41, 0) -8.49%, rgba(42, 41, 41, 0.22) 16.38%, #000000 84.5%)", zIndex: "1" }}>
            <div className="d-flex align-items-center p-2">
              <span style={{ width: "25%" }}> <img className='img-fluid' src={videoData.image} alt="Thumbnail" style={{ maxWidth: "90%", maxHeight: "80%", borderRadius: "2px", border: "2px solid white" }} /></span>
              <span style={{ width: "75%" }}><p className="card-title text-white m-0 ms-2" style={{ fontSize: "1rem", flex: "1", overflow: "hidden", whiteSpace: "nowrap", textOverflow: "ellipsis" }}>
                {videoData.video_title}
                <span className='row p-0 pt-1 prices' style={{ flexWrap: "nowrap", overflow: "hidden" }}>
                  <p className='col ms-2 ps-1 p-0' style={{ color: colorPickers[3].color }}>${videoData.price}</p>
                  <p className='col p-0 ' style={{ color: colorPickers[2].color, textDecoration: 'line-through' }}>${videoData.discount_price}</p>
                  <p className='col p-0'><span className="badge position-absolute p-1" style={{ color: colorPickers[0].color, backgroundColor: colorPickers[1].color, fontWeight: "normal" }}>30%</span></p>
                </span>

              </p>
              </span>
            </div>
          </div>
        )}
      </div>
    </div >
  );
}

export default PlaylistCard;


