import axios from 'axios';
import moment from 'moment/moment';
import React, { useState } from 'react'
import { confirmAlert } from 'react-confirm-alert';
import DataTable from 'react-data-table-component';
import { useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import PlaylistcodeModal from './PlaylistcodeModal';
import { MdOutlineModeEdit } from "react-icons/md";
import { FaCode } from "react-icons/fa6";
import { GrTrash } from "react-icons/gr";
export const PlaylistsTable = ({ data }) => {
    const [playlistData, setPlayListData] = useState(data)
    const user = useSelector(state => state.auth.user)
    const [modal, setModal] = useState(false)
    const [playListData, setPlalistData] = useState({ shortCode: user.short_code, playlistCode: '' })
    const playlistcodeModalOpen = (row) => {
        setModal(true)
        setPlalistData({ shortCode: user.short_code, playlistCode: row?.playlistCode })
    }

    const columns = [
        {
            name: 'Sr',
            selector: row => row.index + 1,
            width: "6%"
        },
        {
            name: 'Playlist Name',
            selector: row => row.name,

        },
        {
            name: 'Created at',
            selector: row => row.createdAt,
            cell: (row) => {
                // Parse the date string with Moment.js
                const momentDate = moment(row.createdAt);

                // Get the relative time from now
                const fromNow = momentDate.fromNow();
                return <>
                    {fromNow}
                </>
            }
        },
        {
            name: 'Code',
            selector: row => row.playlistCode,
        },
        {
            name: 'Attached Videos',
            selector: row => row.videoCount,
        },
        {
            name: 'Action',
            cell: (row) => {
                return <>
                    <div className='d-flex gap-3'>
                        {/* <button className='btn btn-sm btn-outline-dark' style={{ borderRadius: "100px" }} >Edit</button> */}
                        <MdOutlineModeEdit className='pointer_cursor' size={20} onClick={() => setTableedit(true, row.id)} />
                        <FaCode className='pointer_cursor' onClick={() => { playlistcodeModalOpen(row) }} size={20} />
                        <GrTrash className='pointer_cursor' onClick={() => { handleDeletePlayList(row.id, row.name) }} size={20} />
                        {/* <button className='btn btn-sm btn-outline-dark' style={{ borderRadius: "100px" }} >Code</button>
                        <button className='btn btn-sm text-white' style={{ backgroundColor: "#FF3D00", borderRadius: "100px" }} >Delete</button> */}
                    </div>
                </>
            }
        },
    ];

    const navigate = useNavigate()
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);

    const setCreateTable = (value) => {
        searchParams.set('createTable', value);
        navigate({ search: searchParams.toString() });
    };

    const setTableedit = (value, id = null) => {
        searchParams.set('Tableedit', value);
        if (id) {
            searchParams.set('playlistDataId', id);

        } else {
            searchParams.delete('playlistDataId');
        }
        navigate({ search: searchParams.toString() });
    };

    const handleDeletePlayList = (playlistId, name) => {
        confirmAlert({
            // title: `Do you really want to delete the playlist ${name}?`,
            message: `Do you really want to delete the playlist ${name}?`,
            buttons: [
                {
                    label: "Yes, delete",
                    onClick: async () => {
                        await axios.patch("https://liveshopping.asia/deletePlaylistByPlaylistId", {
                            brandId: user.brand_id,
                            playlistId
                        }).then((res) => {
                            if (res.data) {
                                setPlayListData(playlistData.filter((el) => el.id != playlistId))
                                toast.success("Playlist deleted successfully!")

                            }
                        }).catch((Err) => {
                            console.log(Err);
                            toast.error("Something went wrong!")
                        })
                    }
                },
                {
                    label: "Cancel"
                    // onClick: () => alert("Click No")
                }
            ]
        });

    }



    const handleSearchQueryChnage = (query) => {
        setTimeout(() => {
            const filteredData = data?.filter((el) => el?.name.toLowerCase().includes(query.toLowerCase().trim()) || el?.playlistCode.toLowerCase().includes(query.toLowerCase().trim()))

            if (query.length > 0) {
                setPlayListData(filteredData)
            } else {
                setPlayListData(data)
            }
        }, 300);
    }
    const customStyles = {
        headCells: {
            style: {
                backgroundColor: '#F1F1EE', // Set background color for header cells
                color: '#000', // Set text color for header cells
                border: '1px solid #E0E0E0', // Add border to header cells
            },
        },
        cells: {
            style: {
                border: '1px solid #F1F1EE',
                padding: "10px",
            },
        },
    };
    return (
        <>
            <div className='d-flex w-100  align-items-center mb-3 justify-content-between'>
                <div>Playlists</div>
                <div className='d-flex'>
                    <input type='search' className='form-control' style={{ maxWidth: "300px" }} onChange={(e) => { handleSearchQueryChnage(e.target.value) }} placeholder='  Search playlist...' />
                    <button className='btn btn-dark btn-sm text-light bg-dark ms-2' style={{ minWidth: "150px" }} onClick={() => setCreateTable(true)}>Create new Playlist</button>
                </div>
            </div>
            <DataTable
                columns={columns}
                className='card'
                data={playlistData?.map((item, index) => ({ ...item, index }))}
                pagination
                customStyles={customStyles}
            // highlightOnHover
            />
            <PlaylistcodeModal show={modal} handleClose={() => setModal(false)} playListData={playListData} />
        </>
    )
}
